/* Styling for the register page */
.form-container {
  margin-top: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 70vh;
  flex-direction: column;
  background: #f0f0f0; /* Light gray background */
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); /* Subtle box shadow */
  background-image: url(a.jpg);
}

.form-container form {
  padding: 20px;
  width: 300px; /* Adjust form width as needed */
}

.form-container form .title {
  text-align: center;
  margin-bottom: 15px;
  font-weight: bold;
  color: #333; /* Darkened text color for better readability */
}

.form-container form input {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc; /* Light border color */
  border-radius: 5px;
  outline: none;
  transition: border-color 0.3s ease-in-out;
}

.form-container form input::placeholder {
  color: #999; /* Lighter placeholder text color */
}

.form-container form input:focus {
  border-color: #66afe9; /* Highlight border color on focus */
}

.form-container form button {
  width: 100%;
  padding: 12px;
  background-color: #007bff; /* Blue button color */
  color: white;
  border: 1px solid #007bff;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out, border-color 0.3s ease-in-out;
}

.form-container form button:hover {
  background-color: #0056b3; /* Darker blue on hover */
  border-color: #0056b3;
}

/* Styling for the forgot password button */
.forgot-btn {
  background-color: #ccc; /* Light gray background */
  color: #333; /* Dark text color */
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.forgot-btn:hover {
  background-color: #999; /* Darker gray on hover */
}
